.mask
{
    width: 100%;
    height: 100vh;
}

img
{
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    object-fit: cover;
    position: relative;
    box-sizing: border-box;
}
.mask::after
{
    opacity: 0.5;
    background-color: black;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;

}
.hero
{
    width: 100%;
    height: 100%;
    word-wrap: break-word;

}
.hero .content
{
    position: absolute;
    top: 50%;
    left: 40%;
    margin-right: 5%;
}

.hero .content h1
{
font-size: 4rem;
padding: 0.6rem 0 1.5rem;
}
.content .btn
{
    margin-right: 10px;
}
@media screen and  (max-width :640px)
{
   
    .hero .content
    {
        font-size: 10px;
        text-align: center;
    }
    
    .hero .content h1
    {
    font-size: 2rem;
    }
    .hero .content p
    {
    font-size: 1rem;
    }

  .btn
  {
    padding: 8px;
    width: 90px;
    font-size: 10px;
  }
}