.footer
{
    width: 100%;
    padding: 3rem 0;
    background-color: rgba(29, 26, 26, 0.733);
}
.footer-container
{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
}
.left
{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location
{
    display: flex;
}
.right h2
{
    margin-bottom: 10px;
    font-weight: 450;
    color: white;
    
}
.right
{
    margin-left: 10px;
}
.location p
{
    line-height: 30px;
}
.social
{
    margin-top: 20px;
    size: 100px;
}
@media screen and (max-width:500px) {
    .footer-container{
    grid-template-columns: 1fr;
 
}
    
}