.hero-image
{
    width: 100%;
    height: 80vh;
    position: relative;
    background-color: rgba(0, 0, 0, 0.5)
}
.heading h1
{
    font-size: 4rem;
}
.heading
{
    width: 100%;
    height: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: aliceblue;
}
.hero-image::before
{
    content: "";
    background: url("https://images.unsplash.com/photo-1530543787849-128d94430c6b?ixlib=rb-1.2.1&raw_url=true&q=80&fm=jpg&crop=entropy&cs=tinysrgb&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170");
    background-size: cover;
    width: 100%;
    height: 100%;
    position:absolute;
    top: 0;
    left: 0;
    z-index: -1;

}
@media screen and (max-width:600px) {
    .heading h1
    {
        font-size: 2rem;
    }
    .hero-image p
    {
        font-size: 1rem;
        text-align: center;
    }
}
