label
{
    color: white;
    margin-top: 2%;
    margin-bottom: 1%;

}
form
{
display: flex;
flex-direction: column;
margin: auto;
max-width: 600px;
padding: 4rem 1rem;
margin: auto;
}
form input , form textarea
{
    margin-bottom: 1rem;
    background-color: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.2);
    color: white;
    font-size: 1.2rem;
}
form input
{
    height: 50px;
}
form textarea
{
    height: 150px;
}

