.project-heading
{
    text-align: center;
}
.project-container
{
display: flex;
flex-direction: row;
padding: 5%;
text-align: center;
background-color: black;
padding-bottom: 10%;
}
.project-card
{
    width: 80%;
    background-color: rgba(40, 36, 36, 0.557);
    margin: 2%;
    max-height: 70%;
}
.project-card p
{
    padding: 10px;
}
.myimg
{
    height: 60%;
}
@media screen and (max-width:500px) {
.project-container
{
    flex-direction: column;
    font-size: small;
    margin-left: 10%;
}
}
.btns
{
padding-top: 20px;
margin-bottom: 10%;
margin-top: 5px;
}
h1
{
    font-size: large;
    padding-bottom: 10px;
}
.prject-details
{
    margin: 20px;
}