.header
{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    z-index: 10;
    position: fixed;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.703);
}
.nav-menu
{
    display: flex;
}
.nav-menu li
{
padding: 0 1rem;
}
#ham1
{
    display: none;
}
.nav-menu li a
{
    font-size: 1.2rem;
    font-weight: 500;
}
@media screen and (max-width:900px)
 {
    .nav-menu
    {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background:rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
    }
    .nav-menu.active
    {
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a
    {
font-size: 2rem;
    }
    #ham1
    {
    display: initial;
    }
}
